import React from 'react'
import styled from 'styled-components'
import roots from '../images/roots.png'
import bg5 from '../images/bg5.png'
import {cutitTocare, cutitPaine, cutitChef, cutitMacelar, cutitPeste, cutitFilat} from './DataSection'
import { useState } from 'react'
import { useEffect } from 'react'
import KnifeTypes from './KnifeTypes'

export default function ShopSection() {
    const [selected, setSelected] = useState('tocare')
    const [data, setData] = useState([])

    useEffect(() => {
        switch(selected) {
            case 'tocare':
                setData(cutitTocare)
                break
            case 'paine':
                setData(cutitPaine)
                break
            case 'chef':
                setData(cutitChef)
                break
            case 'macelar':
                setData(cutitMacelar)
                break
            case 'filat':
                setData(cutitFilat)
                break
            case 'peste':
                setData(cutitPeste)
                break
            default:
                setData(cutitTocare)
                break
        }
    }, [])

    const list = [
        {
            id: 'tocare',
            title: 'Tocare'
        },
        {
            id: 'paine',
            title: 'Pâine'
        },
        {
            id: 'chef',
            title: 'Chef'
        },
        {
            id: 'macelar',
            title: 'Măcelar'
        },
        {
            id: 'filat',
            title: 'Filat'
        },
        {
            id: 'peste',
            title: 'Pește'
        }
    ]

    return (
        <Container>
            <RootsImg src={roots} alt=""/>

            <Wrapper>
                <TopSide>
                    <H1>Title</H1>
                    <Ul>
                        {
                            list.map((knife, i) => (
                                <KnifeTypes key={i} title={knife.title} active={selected === knife.id} id={knife.id} setSelected={setSelected}/>
                            ))
                        }
                    </Ul>
                </TopSide>

                <BottomSide>
                    <Description>ddfsdf sdfsdfsdfsd fsdfsdfdsffs fsdfsd fsdfsd fsdf fdfsfdfsdf sdf</Description>

                    <ProductContainer>
                        {
                            data.map((knife, i) => (
                                <KnifeCard key={i}>
                                    <KnifeImg src={knife.img} alt=""/>
                                    <TextContainer>
                                        <KnifeTitle>{knife.title}</KnifeTitle>
                                        <KnifeDescription>{knife.description}</KnifeDescription>
                                    </TextContainer>
                                </KnifeCard>
                            ))
                        }
                    </ProductContainer>
                </BottomSide>
            </Wrapper>
        </Container>
    )
}



const Container = styled.section`
    min-height: 100vh;
    position: relative;
    background:  url(${bg5});
`
const RootsImg = styled.img`
    position: absolute;
    width: 100%;
    top: -20px;
    left: 0;
    z-index: 0;
    -webkit-mask-image: linear-gradient(to bottom, transparent 0px, black 20px);
    mask-image: linear-gradient(to bottom, transparent 0px, black 20px);
`
const Wrapper = styled.div`
    min-height: 100vh;
    display: flex;
    flex-direction: column;
`
const TopSide = styled.div`
    display: flex;
    align-items: center;
    flex: 1;
`
const H1 = styled.h1`

`
const Ul = styled.ul`
    padding: 0;
    display: flex;
`
const BottomSide = styled.div`
    display: flex;
    flex: 1;
    align-items: flex-end;
    justify-content: flex-end;
`
const Description = styled.p`

`
const ProductContainer = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    >div:nth-child(even) img {
        filter: brightness(70%);
    }
    >div:nth-child(odd) img {
        filter: brightness(80%);
    }
`
const KnifeCard = styled.div`
    flex: 0 1 300px;
    height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: all 1s;
    &:hover {
        background-color: var(--c1h);
        img {
            opacity: 0.2;
            z-index: 0;
        }
    }
`
const KnifeImg = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
`
const TextContainer = styled.div`
    position: absolute;
`
const KnifeTitle = styled.h3`
    font-size: 1.4rem;
    text-align: center;
`
const KnifeDescription = styled.p`
    font-size: 0.9rem;
    text-align: center;
`